a.photo {
  box-sizing: border-box;
  border-radius: 0px 0px 10px 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  height: 220px;
  overflow: hidden;
}

a.photo:hover {
  box-shadow: 0px 0px 20px #666666;
  background-color: #666666;
}

a.photo > * {
  position: relative;
}

a.photo p {
  padding: 0px 5px;
  margin: 0px;
}

a.photo p.photoTitle {
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

a.photo img {
  width: 100%;
  height: auto;
}
