header {
  font-size: 60px;
  position: relative;
  width: 100%;
  min-height: 200px;
  font-weight: bold;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  border-bottom: 2px solid #000000;
  flex-wrap: wrap;
  word-break: break-word;
}

header p {
  position: relative;
  text-align: center;
}

@media (max-width: 900px) {
  header {
    font-size: 35px;
  }
}
