a.album {
  box-sizing: border-box;
  border-radius: 0px 0px 10px 10px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  position: relative;
  height: 250px;
  overflow: hidden;
}

a.album:hover {
  box-shadow: 0px 0px 20px #666666;
  background-color: #666666;
}

a.album > * {
  position: relative;
}

a.album p {
  padding: 0px 5px;
  margin: 0px;
}

a.album p.albumTitle {
  font-weight:bold;
  overflow: hidden;
  display: -webkit-box;
  -webkit-line-clamp: 3;
  -webkit-box-orient: vertical;
}

a.album p.amountOfPhotos {
  height: 20px;
}

a.album img {
  width: 100%;
  height: auto;
}
