#navbar {
  font-size: 1.5em;
  font-weight: bold;
  position: relative;
  box-sizing: border-box;
  border-bottom: 2px solid #000000;
  width: 100%;
  height: 50px;
  background-color: #777777;
  vertical-align: middle;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-content: center;
}

#navbar #navbarBack {
  position: absolute;
  border-radius: 0px 15px 15px 0px;
  left: 0px;
  top: 0px;
  width: 175px;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 2;
  background-color: #07afd9;
  cursor: pointer;
}

#navbar #navbarTitle {
  position: relative;
  width: 100%;
  display: flex;
  height: 100%;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

@media(hover: hover) and (pointer: fine) {
  #navbar #navbarBack:hover {
    background-color: #07d8f0;
  }
}

@media (max-width: 900px) {
  #navbar {
    flex-direction: column;
    height: 100px;
  }
  #navbar #navbarBack {
    border-radius: 0px;
    position: relative;
    width: 100%;
    height: 50%;
  }

  #navbar #navbarTitle {
    height: 50%;
  }
}
