a.user {
  box-sizing: border-box;
  border-radius: 10px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: flex-start;
  position: relative;
  height: 200px;
  overflow: hidden;
}

a.user:hover {
  box-shadow: 0px 0px 20px #666666;
  background-color: #666666;
}

a.user > * {
  position: relative;
}

a.user p {
  padding: 0px 5px;
  margin: 0px;
  font-weight: bold;
}

a.user img {
  width: 100%;
  height: auto;
  border-radius: 50%;
}
