#popupCover {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0px;
  left: 0px;
  background-color: #000000;
  z-index: 999;
  opacity: 0.9;
}

#popup {
  width:100%;
  height:100%;
  position:fixed;
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  left:50%;
  top:50%;
  transform: translate(-50%, -50%);
  z-index: 1000;
}

#popup #popupLeft {
  border-radius: 50%;
  position: relative;
  width: 75px;
  height: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  z-index: 2;
}

#popup #popupLeft svg {
  transform: rotate(90deg);
}

#popup #popupRight {
  border-radius: 50%;
  position: relative;
  width: 75px;
  height: 75px;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  overflow: hidden;
  z-index: 2;
}

#popup #popupRight svg {
  transform: rotate(270deg);
}

#popup img {
  position: relative;
  margin: 0px 50px;
  width: 600px;
  height: 600px;
  border: 5px solid #FFFFFF;
  z-index: 1;
}

@media (max-width: 900px) {
  #popup img {
    position: absolute;
    margin: 0px 10px;
    max-width: 90%;
    height: auto;
  }

  #popup #popupLeft {
    position: absolute;
    left: 0px;
    top: 50%;
    transform: translate(0, -50%);
    margin: 0px 0px 0px 5px;
  }

  #popup #popupRight {
    position: absolute;
    right: 0px;
    top: 50%;
    transform: translate(0, -50%);
    margin: 0px 5px 0px 0px;
  }
}

@media(hover: hover) and (pointer: fine) {
  #popup #popupLeft:hover {
    box-shadow: 0px 0px 20px rgba(255,255,255,0.2);
    background-color: rgba(255,255,255,0.2);
  }

  #popup #popupRight:hover {
    box-shadow: 0px 0px 20px rgba(255,255,255,0.2);
    background-color: rgba(255,255,255,0.2);
  }
}
